/*
 * Github color scale
 */
.color-github-disabled {
  fill: red;
}
.color-github-empty {
  fill: #eeeeee;
}
.color-github-0 {
  fill: #eeeeee;
}
.color-github-1 {
  fill: #d6e685;
}
.color-github-2 {
  fill: #8cc665;
}
.color-github-3 {
  fill: #44a340;
}
.color-github-4 {
  fill: #1e6823;
}



